import { ReactNode } from 'react';
import styles from '../ui/style/BlendedGrid.module.css';

interface BannerProps {
  children?: ReactNode;
  className?: string;
}

export const BlendedGrid = ({ children }: { children: ReactNode }) => {
  return (
    <div className={styles.blendedGrid}>
      {children}
    </div>
  );
};

export const TopBanner = ({ children, className = '' }: BannerProps) => (
  <div className={`${styles.topBanner} ${className}`}>{children}</div>
);

export const MiddleBanner = ({ children, className = '' }: BannerProps) => (
  <div className={`${styles.middleBanner} ${className}`}>{children}</div>
);

export const LeftBanner = ({ children, className = '' }: BannerProps) => (
  <div className={`${styles.leftBanner} ${className}`}>{children}</div>
);

export const CenterBanner = ({ children, className = '' }: BannerProps) => (
  <div className={`${styles.centerBanner} ${className}`}>{children}</div>
);

export const RightBanner = ({ children, className = '' }: BannerProps) => (
  <div className={`${styles.rightBanner} ${className}`}>{children}</div>
);

export const BottomBanner = ({ children, className = '' }: BannerProps) => (
  <div className={`${styles.bottomBanner} ${className}`}>{children}</div>
);